import { pricingInverse } from '../../pricing';

// Initial State
const initialState = { clothes: [], total: 0 };

const OrderReducer = (state = initialState, action) => {
  switch (action.type) {
    case 'ORDER.ADD_ITEM':
      return {
        ...state,
        clothes: [...state.clothes, action.item],
      };
    case 'ORDER.TOGGLE_ALTERATION': {
      const { itemIndex, alterationId } = action;
      const newItem = { ...state.clothes[itemIndex] };
      newItem.alterations = newItem.alterations || {};
      newItem.alterations[alterationId] = newItem.alterations[alterationId] > 0 ? 0 : 1;
      const newItems = [...state.clothes];
      newItems[itemIndex] = newItem;
      return { ...state, clothes: newItems };
    }
    case 'ORDER.SET_ALTERATION_QUANITY': {
      const { itemIndex, alterationId } = action;
      const newItem = { ...state.clothes[itemIndex] };
      newItem.alterations = newItem.alterations || {};
      newItem.alterations[alterationId] = action.quantity;
      const newItems = [...state.clothes];
      newItems[itemIndex] = newItem;
      return { ...state, clothes: newItems };
    }
    case 'ORDER.REMOVE_ITEM_BY_SLUG': {
      const clothesWithoutSlug = state.clothes.filter(cloth => cloth.slug !== action.slug);
      const clothesWithSlug = state.clothes.filter(cloth => cloth.slug === action.slug);
      clothesWithSlug.pop();
      return {
        ...state,
        clothes: [...clothesWithoutSlug, ...clothesWithSlug],
      };
    }
    case 'ORDER.DELETE_ITEM':
      return {
        ...state,
        clothes: state.clothes.filter((c, index) => index !== action.itemId),
      };
    default:
      return state;
  }
};

/* Selectors */

// Get all orders
export const getClothes = state => state.order.clothes;

export const getTotal = state =>
  state.order.clothes.reduce((total, cloth) => {
    const alterationsKeys = Object.keys(cloth.alterations || {});

    return total + alterationsKeys.reduce((sum, alterationId) =>
      sum + (cloth.alterations[alterationId] * (pricingInverse[alterationId].price || 0)), 0);
  }, 0);

// Export Reducer
export default OrderReducer;



